header {
  margin: -5px 10px;
  @extend %flexSpaceBetween;
  align-items: center;
  .header__left {
    font-size: 35px;
    font-weight: bold;
    text-transform: uppercase;
  }
  .header__middle {
    @extend %flexCenterCenter;
    div {
      @extend %flexCenterCenter;
      @extend %fullscreens;
      &:nth-of-type(1) > span {
        background-color: $bg2;
      }
      &:nth-of-type(2) > span {
        background-color: $bg1;
      }
      &:nth-of-type(3) > span {
        background-color: $bg6;
      }
      &:nth-of-type(4) > span {
        background-color: $bg3;
      }
      &:nth-of-type(5) > span {
        background-color: $bg4;
      }
      &:nth-of-type(6) > span {
        background-color: $bg7;
      }
      &:nth-of-type(7) > span {
        background-color: $bg5;
      }
      span {
        display: block;
        width: 20px;
        height: 15px;
        border-radius: 50%;
        margin-right: 5px;
      }
      div {
        text-transform: uppercase;
        margin-top: -3px;
        margin-right: 10px;
        font-size: 21px;
        font-weight: 500;
      }
    }
  }

  .header__right {
    @extend %flexCenterCenter;
    div {
      margin-right: 10px;
    }
  }
}
main {
  display: grid;
  grid-template-columns: 1fr;
  .posts {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: auto;
    @extend %fullscreens;
    .post {
      &:nth-of-type(odd) {
        background-color: $bg1;
      }
      &:nth-of-type(even) {
        background-color: $bg2;
      }
      &:nth-of-type(3n) {
        background-color: white;
      }
      &:nth-of-type(4n) {
        background-color: $bg3;
      }
      &:nth-of-type(5n) {
        background-color: $bg4;
      }
      &:nth-of-type(7n) {
        background-color: $bg5;
      }
      box-sizing: border-box;
      width: 100%;
      height: 50vh;
      display: flex;
      flex-direction: column;
      font-size: 12px;
      padding: 15px;
      .post__date {
        @extend %flexSpaceBetween;
      }
      .post__content {
        @extend %flexCenterCenter;
        flex-direction: column;
        height: 100%;
        img {
          width: 50%;
        }
        div {
          margin-top: 10px;
        }
      }
      .post__footer {
        @extend %flexSpaceBetween;
      }
    }
  }
}

footer {
  .footer__frist {
    width: 100%;
    height: 20vh;
    @extend %flexCenterCenter;
    a {
      color: #000052;
      font-weight: 500;
    }
  }
  .footer__end {
    height: 10vh;
    @extend %flexSpaceBetween;
    padding: 10px;
    div {
      display: flex;
      flex-direction: column;
      &:nth-of-type(2) > a {
        text-align: center;
      }
    }
    a {
      color: #000052;
      font-weight: 500;
    }
  }
}
